.App {
  font-family: "Tahoma";
}

.Nav-Bar {
  display: flex;
  position: sticky;
  justify-content: space-evenly;
  align-items: center;
  list-style-type: none;
  z-index: 1;
  top: 0px;
  opacity: 0.9;
  min-height: 3.5vh;
  margin: 0px;
  background: rgb(255, 140, 26);
}

.Nav-Kids {
  flex: 1;
  align-items: center;
  text-align: center;
}

.Nav-Kids {
color: white;
}

.Home-Page {
  display: flex;
}

.Intro-Writing {
  display: flex;
  flex: 1;
  flex-direction: column;
  position: absolute;
  text-align: center; 
  color: white;
  font-family: "Tahoma";
  font-style: italic;
  align-content: center;
  font-size: 25px;
  top: 25%;
  left: 25%;
  right: 25%;
  bottom: 25%;
}

.Background {
  width: 100%;
  height: 100%;
  position: relative;
}

.Skills-Container {
  height: 400px;
  width: 100%;
  padding: 0;
  background-color: orange;
}